import React, {Component} from 'react';
import worldMap from './Images/worldMap.png';
import './Attribute.css';


class Attribute extends Component{

    constructor(props){
        super(props);
        this.state = {displayImage: this.props.displayImage, subject: this.props.subject, subjectContent: this.props.subjectContent};

    }

    render(){
        return(
            <div className='attributeDiv'>
                <img className='attributeImage' src={this.state.displayImage} alt='Error'></img>
                <div className='attributeText'>
                    <p className='subject'>{this.state.subject}</p>
                    <p className='subjectContent'>{this.state.subjectContent}</p>
                </div>
            </div>
            
        );
    }

    
}

export default Attribute;