import React, {Component} from 'react';

class Achievement extends Component{

    constructor(props){
        super(props);
        this.state={header: props.header, content: props.content, active: 'no'};
        this.handleClick = this.handleClick.bind(this);
    }

    componentDidMount(){
        this.refs.achieve.style.display = 'none';
        this.props.onRef(this);
    }

    deselect(){
        this.setState({active: 'no'});
        this.refs.achieve.style.display = 'none';

    }

    select(){
        if(this.state.active === 'no'){
            this.setState({active: 'yes'});
        }
        else{
            this.setState({active: 'no'});
        }
        
    }

    selected(){
        if(this.state.active === 'yes'){
            return 'list-group-item list-group-item-action active';
        }
        else{
            return 'list-group-item list-group-item-action';
        }
    }

    render(){
        return(
            <li className={this.selected()} onClick={this.handleClick}>
                {this.state.header}
                <div className='achievementContent' ref='achieve'>{this.state.content}</div>
            </li>
        );
    }

    handleClick(){
        this.props.onClick();
        if(this.refs.achieve.style.display === 'none'){
            this.refs.achieve.style.display = 'block';
        }
        else{
            this.refs.achieve.style.display = 'none';
        }
            
        
    }
}

export default Achievement;