import React, {Component} from 'react';
import './SigAchievements.css';
import Achievement from './Achievement';

const defaultPic = 'https://images.pexels.com/photos/274131/pexels-photo-274131.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500';
const pic1 = 'https://images.unsplash.com/photo-1496154704336-6c88a94a019a?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&w=1000&q=80';
const pic2 = 'data:image/jpeg;base64,';
const pic3 = 'https://i.imgur.com/vlmUXyk.jpg';

class SigAchievements extends Component{
    constructor(){
        super();
        this.handleClick = this.handleClick.bind(this);
        this.state=({image: defaultPic, children: []})
    }

    render(){
        return(
            <div className='achievementDiv'>
                <ul className='list-group'>
                    <Achievement onRef={ref=>(this.state.children['child1']=ref) } header='Item 1' content='This is the first achievement' 
                    onClick={()=>this.handleClick(pic1, 'child1')}></Achievement>
                    <Achievement onRef={ref=>(this.state.children['child2']=ref)} header='Item 2' content='This is the second achievement' 
                    onClick={()=>this.handleClick(pic2, 'child2')}></Achievement>
                    <Achievement onRef={ref=>(this.state.children['child3']=ref)} header='Item 3' content = 'This is the 3rd item in the list'
                    onClick={()=>this.handleClick(pic3, 'child3')}></Achievement> 
                </ul>
                <img ref='image' className='achievementImage' src={this.state.image}></img>
            </div>
        );
    }

    handleClick(newImage, child){
        this.state.children[child].select();
        const childrenArray = Object.entries(this.state.children);
        for(const [index, element] of childrenArray){
            if(index !== child){
                element.deselect();
            }
        }
        if(this.state.image !== newImage){
            this.setState({image: newImage});
        }
        else{
            this.setState({image: defaultPic})
        }
        
    }
}

export default SigAchievements;